.main-button-text {
  align-items: center;
  font-size:500%;
  font-family: 'Luminari, fantasy';
}

.go-next-page-button-text {
  align-items: center;
  font-size:250%;
  font-family: 'Luminari, fantasy';
}

.go-to-listpage {
  position: absolute;
  aspect-ratio: 15/3;
  bottom: 5vh;
  right: 5vw;
  width: clamp(120px, 15vw, 300px);
  background-color: orange;
  border: none;
  border-radius: 12px;
}

.main-button {
  aspect-ratio: 15/3;
  width: clamp(150px, 20vw, 400px);
  background-color: aqua;
  border: none;
  border-radius: 12px;
  min-height: 50px;   /* precisa do 'px' */
  height: auto;       /* pode at√© remover para evitar conflito */
}

.App-header {
  height: 100vh;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
